






































import Vue from 'vue'
import { mapState, mapMutations } from 'vuex'
import { getUserInfo } from '@/services/user'
export default Vue.extend({
  data() {
    return {
      userInfo: {}
    }
  },
  created() {
    console.log(this.$route)
    this.loadUserInfo()
    this.loadUserInfo()
  },
  computed: {
    ...mapState(['isCollapse']),
    navs() {
      const temps: any[] = []
      this.$route.matched.filter((item) => {
        if (item.name) {
          temps.push({
            name: item.name,
            title: item.meta.title
          })
        }
      })
      console.log(temps)
      return temps
    }
  },
  methods: {
    ...mapMutations(['setUser', 'setCollapse']),
    async loadUserInfo() {
      const { data } = await getUserInfo()
      if (data.state) {
        this.userInfo = data.content
      }
    },
    loginout() {
      this.$confirm('确定要退出吗？', '提示')
        .then(() => {
          this.setUser(null)
          this.$router.push({
            name: 'Login'
          })
          this.$message({
            type: 'success',
            message: '退出成功'
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消退出'
          })
        })
    }
  }
})
